import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";

// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";
export default class MultiStepForm extends Component {
  state = {
    // step1
    firstName: "",
    lastName: "",

    //step 2
    company_name: "",

    employees: "",
    industry: "",

    // step4

    // step5

    // step6

    // step7

    // step8

    // step9
    phone: "",

    // step10
    email: "",

    // step18
    dob: "",

    // step25
    agree: "",
  };
  handleChangeField = (e, name, value) => {
    this.setState(
      {
        [name]: value,
      },
      () => console.log(this.state)
    );
  };

  submitData() {
    const data = {
      agree: this.state.agree,
    };
    axios
      .post("https://hooks.zapier.com/hooks/catch/1712527/o5y1jta/", data)
      .then((response) => console.log(response))
      .finally(() => console.log("Data Sent", { data: data }));
  }

  renderStep = (props) => {
    const { history } = props;
    const { path, params } = props.match;
    if (params.step === "thankyou") {
      this.submitData();
      return <ThankYou />;
    } else if (params.step === "decline") {
      return <Decline />;
    }
    const step = parseInt(params.step);
    const currentPath = `/${path.split("/")[1]}`;
    switch (step) {
      case 1:
        return (
          <Step1
            history={history}
            nextStep={`${currentPath}/2`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 2:
        return (
          <Step2
            history={history}
            prevStep={`${currentPath}/1`}
            nextStep={`${currentPath}/3`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 3:
        console.log("step 3");

        return (
          <Step3
            history={history}
            prevStep={`${currentPath}/2`}
            nextStep={`${currentPath}/4`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 4:
        console.log("step 4");
        return (
          <Step4
            history={history}
            prevStep={`${currentPath}/3`}
            nextStep={`${currentPath}/5`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 5:
        return (
          <Step5
            history={history}
            prevStep={`${currentPath}/4`}
            nextStep={`${currentPath}/6`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 6:
        return (
          <Step6
            history={history}
            prevStep={`${currentPath}/5`}
            nextStep={`${currentPath}/thankyou`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );

      default:
        return <div>invalid step</div>;
    }
  };
  render() {
    const { path } = this.props.match;
    return (
      <Switch>
        <Route
          path="/start"
          exact
          render={() => <Redirect to={`${path}/1`} />}
        />
        <Route
          path={`${path}/:step`}
          render={(props) => (
            <>
              {this.renderStep(props)}
              <small
                style={{
                  position: "absolute",
                  width: "100%",
                  bottom: 0,
                  textAlign: "center",
                  padding: 10,
                }}
              >
                SBA Disasters Pros is in no way shape or form affiliated with
                the U.S. Government or the Small Business Administration
              </small>
            </>
          )}
        />
      </Switch>
    );
  }
}
