import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step15(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step
      heading={`Fantastic! About how many employees does ${state.company_name} Curently have employed?`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "employees", "1 - 5");
                    history.push(nextStep);
                  }}
                >
                  1 - 5
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "employees", "5 - 10");
                    history.push(nextStep);
                  }}
                >
                  5 - 10
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "employees", "10 - 20");
                    history.push(nextStep);
                  }}
                >
                  10 - 20
                </MDBBtn>
              </MDBCol>{" "}
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "employees", "20 - 50");
                    history.push(nextStep);
                  }}
                >
                  20 - 50
                </MDBBtn>
              </MDBCol>
            </MDBRow>
            <MDBRow center className="mt-3">
              <MDBCol lg={6}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "employees", "50 - 100");
                    history.push(nextStep);
                  }}
                >
                  50 - 100
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={6}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "employees", "100+");
                    history.push(nextStep);
                  }}
                >
                  100+
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
